'use client';

import { useRouter } from 'next/router';
import { useState, useEffect, useRef, useCallback } from 'react';
import { useAuth } from '../../context/AuthContext';

export default function SignUp() {
    const router = useRouter();
    const { is_authenticated } = useAuth();
    const [name, set_name] = useState('');
    const [email, set_email] = useState('');
    const [password, set_password] = useState('');
    const [confirm_password, set_confirm_password] = useState('');
    const [error, set_error] = useState('');
    const [loading, set_loading] = useState(false);
    const google_initialized = useRef(false);

    // 1. MANUAL SIGNUP START
    const handle_manual_signup = async () => {
        if (password !== confirm_password) {
            set_error('Passwords do not match');
            return;
        }
        set_loading(true);
        set_error('');
        try {
            const res = await fetch('http://localhost:5000/api/auth/customer/signup/manual/start', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ email }),
            });
            const data = await res.json();

            if (res.status === 409) {
                set_error(data.message);
                return;
            }
            if (!res.ok) throw new Error(data.message);

            sessionStorage.setItem('cc_signup_temp', JSON.stringify({ name, email, password }));
            router.push({ pathname: '/customer/verify-otp', query: { email, intent: 'manual_signup' } });
        } catch (err: any) {
            set_error(err.message);
        } finally {
            set_loading(false);
        }
    };

    // 2. GOOGLE SIGNUP START
    const handle_google_response = useCallback(async (response: any) => {
        set_loading(true);
        set_error('');
        try {
            const res = await fetch('http://localhost:5000/api/auth/customer/signup/google/start', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ token: response.credential }),
            });
            const data = await res.json();

            if (res.status === 409) {
                set_error(data.message);
                return;
            }
            if (!res.ok) throw new Error(data.message);

            sessionStorage.setItem('cc_google_cred', response.credential);
            router.push({ pathname: '/customer/verify-otp', query: { email: data.email, intent: 'google_signup' } });
        } catch (err: any) {
            set_error(err.message);
        } finally {
            set_loading(false);
        }
    }, [router]);

    // Google Init... (omitted for brevity, same as signin logic)
    // Google Init... (omitted for brevity, same as signin logic)
    const init_google = useCallback(() => {
        if (is_authenticated) return;

        const client_id = process.env.NEXT_PUBLIC_GOOGLE_CLIENT_ID;
        const container = document.getElementById('google-btn-container-signup'); // Double check this ID matches your JSX

        if (!window.google?.accounts?.id || !container || google_initialized.current) return;

        try {
            window.google.accounts.id.initialize({
                client_id: client_id!,
                callback: handle_google_response,
                ux_mode: 'popup',
            });
            window.google.accounts.id.renderButton(container, {
                theme: 'outline',
                size: 'large',
                width: 320
            });
            google_initialized.current = true;
        } catch (err) {
            console.error('Google signup init failed:', err);
        }
    }, [handle_google_response, is_authenticated]);

    useEffect(() => {
        // Force a small delay to ensure DOM is ready
        const timer = setTimeout(() => {
            if (typeof window !== 'undefined' && window.google) {
                init_google();
            }
        }, 100);
        return () => clearTimeout(timer);
    }, [init_google]);

    return (
        <div className="signup-page">
            <div className="signup-container">
                <h1 className="signup-title">Sign Up</h1>
                {error && <p style={{ color: 'red', textAlign: 'center' }}>{error}</p>}

                <div className="signup-input-field">
                    <input type="text" value={name} onChange={(e) => set_name(e.target.value)} placeholder="Name" />
                </div>
                <div className="signup-input-field">
                    <input type="email" value={email} onChange={(e) => set_email(e.target.value)} placeholder="Email" />
                </div>
                <div className="signup-input-field">
                    <input type="password" value={password} onChange={(e) => set_password(e.target.value)} placeholder="Password" />
                </div>
                <div className="signup-input-field">
                    <input type="password" value={confirm_password} onChange={(e) => set_confirm_password(e.target.value)} placeholder="Confirm Password" />
                </div>

                <button onClick={handle_manual_signup} disabled={loading} className="signup-button">
                    {loading ? 'Processing...' : 'Sign Up'}
                </button>

                <div id="google-btn-container-signup" style={{ marginTop: '20px' }} />
            </div>
        </div>
    );
}